import { HospdetailsPage } from './../hospdetails/hospdetails';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the FhospitalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-fhospital',
  templateUrl: 'fhospital.html',
})
export class FhospitalPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FhospitalPage');
  }

  goHospitalDetails()
  {
    this.navCtrl.push(HospdetailsPage);
  }

}



